.top{
    width: 100%;
    height: 50px;
    background-color: white;
    position: sticky;
    top: 0;
    display: flex;
    align-items: center;
    font-family:'Lora', serif;
    z-index: 3;

}
.topLeft , .topRight{
    flex:3;
    display:flex;
    align-items: center;
    justify-content: center;

}
.topCentre{
    flex:6;
}
.topImage{
    width:40px;
    height:40px;
    border-radius: 50%;
    object-fit: cover;
   
 }

 .topIcon{
    font-size: 20px;
    margin-right: 20px;
    color:#444;
    cursor:pointer;
 }

 .topList{
    display:flex;
    justify-content:center;
    margin:0;
    padding:0;
    list-style: none;
    color:#444;
 }

 .topListItem{
    margin-right:20px;
    font-weight:300;
    font-size:18px;
    cursor:pointer;
 }
.topSearchIcon{
    font-size:18px;
    color:#444;
    cursor:pointer;
    margin-left:10px;
}



