.post{
    width:250px;
    margin: 0px 25px 40px 25px;
    
}

.postImg{

    width: 120%;
    height: 280px;
    object-fit: cover;
    border-radius: 7px;
    
    
}

.postInfo{
    display: flex;
    flex-direction: column;
    align-items: center;

}

.postCat{
    font-family: 'Varela', sans-serif;
    font-size: 11px;
    color:#be9656;
    line-height:20px;
    margin-top: 15px;
    margin-right: 10px;
    cursor: pointer;
}

.postTitle{
    font-family: 'Sofia Sans Semi Condensed', sans-serif;
    font-size:24px;
    font-weight: 700;
    margin-top: 10px;
    cursor:pointer;

}

.postDate{
    font-family: 'Lora', serif;
    font-style: italic;
    font-size:13px;
    color:#999;
    margin-top:15px;
}

.postDesc{
    font-family: 'Varela', sans-serif;
    font-size: 14px;
    color:#444;
    line-height: 24px;
    margin-top: 15px;
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 4;
    -webkit-box-orient: vertical;


}